import React from 'react';

import MakerMade3D from '../../images/MakerMade3D.png';
import Makerverse from '../../images/Makerverse.png';
import ChinguTabs from '../../images/ChinguTabs.jpg';
import SupportChat from '../../images/SupportChat.PNG';
import UserVoice from '../../images/UserVoice.PNG';
import ITK from '../../images/ITK.PNG';

import Card from './Card';

const Projects = () => (
  <div className="projects" id="projects">
    <div className="title">
      <h1>Projects</h1>
    </div>
    <div className="projectContainer">
      <Card
        title="Support Bot"
        description="A Support Bot available to 1.5 million users, providing support for an array of tasks,
        resulting in fewer support tickets and an increase in productivity."
        skills={[
          'HTML',
          'CSS',
          'ES6',
          'ReactJS',
          'Node',
          'Express',
          'Azure Bot Service',
          'MongoDB',
          'SAML'
        ]}
        image={SupportChat}
      />
      <Card
        title="Associate Feedback Platform"
        description="A dashboard for users to visit and provide feedback for products they use everyday, 
        helping improve preferred products and replace others."
        skills={['HTML', 'CSS', 'ES6']}
        image={UserVoice}
      />
      <Card
        title="Makerverse"
        description="A UI built to send G-Code commands to CNC controllers."
        skills={['HTML', 'CSS', 'ES6', 'ReactJS', 'Node', 'Electron']}
        image={Makerverse}
        live="http://www.makerverse.com/"
      />
      <Card
        title="MakerMade3D"
        description="An e-commerce portal that deals with selling of 3D printers while offering assembly and software support."
        skills={['HTML', 'CSS', 'ES6', 'ReactJS', 'Node']}
        image={MakerMade3D}
        live="http://www.makermade3d.com/"
      />
      <Card
        title="Associate Notifications"
        description="A resource for users to subscribe to mobile and desktop notifications for products they are interested in."
        skills={['HTML', 'React', 'Express', 'OAuth', 'CSS', 'ES6']}
        image={ITK}
      />
      <Card
        title="ChinguTabs"
        description="A remote team project based off the popular chrome extension, Momentum."
        skills={['HTML', 'CSS', 'ES6', 'ReactJS', 'Node', 'Express', 'MongoDB']}
        image={ChinguTabs}
        live="https://chrome.google.com/webstore/search/chingutabs"
        github="https://github.com/chingu-voyage4/Bears-Team-13"
      />
    </div>
  </div>
);

export default Projects;
